/* Import Tailwind CSS */
/* Global Styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: white;
  color: black;
}

/* Container Styling */
.container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  min-height: 100vh;
  padding: 20px;
  background-color: #f4f4f4;
}

/* Card Layout */
.card {
  width: 1400px;
  padding: 20px;
  background-color: white;
  border: 2px solid black;
  border-radius: 10px;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.1);
}

/* Form Layout */
form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* Form Sections */
.form-section {
  display: grid;
  gap: 20px;
  padding-bottom: 20px;
  border-bottom: 2px solid #ccc;
}

.form-section.three-cols {
  grid-template-columns: repeat(3, 1fr);
}

.form-section.two-cols {
  grid-template-columns: repeat(2, 1fr);
}

/* Input and Select Styling */
input, select {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: white;
  color: black;
}

label {
  font-weight: bold;
  margin-bottom: 5px;
}

/* Button Styling */
button {
  padding: 12px;
  border-radius: 5px;
  background-color: rgba(209, 163, 60, 1);
  color: black;
  font-size: 16px;
  font-weight: bold;
  border: none;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.1s ease;
}

/* Button Hover */
button:hover {
  background-color: rgba(189, 143, 40, 1);
  transform: scale(1.05);
}

/* Disabled Button */
button:disabled {
  background-color: rgba(209, 163, 60, 0.5);
  cursor: not-allowed;
}

/* Reset Button (Top Right) */
.reset-button {
  position: absolute;
  top: 20px;
  right: 20px;
}

/* Title Styling */
.title {
  text-align: center;
  font-size: 24px;
  font-weight: bold;
  text-transform: uppercase;
  padding-bottom: 20px;
  border-bottom: 2px solid #ccc;
}

/* Calculations Section */
.calculations {
  margin-top: 20px;
  font-size: 16px;
  line-height: 1.6;
}

.calculations p {
  margin: 5px 0;
}

.calculations strong {
  font-weight: bold;
}

/* Notes Section */
.notes {
  width: 100%;
  height: 80px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: white;
  font-size: 16px;
}
